.ol-zoom .out-in-horizontal {
    display: inline-flex;
}

.ol-zoom .ol-zoom-level {
    display: block;
    text-align: center;
}

.out-in-horizontal .ol-zoom-in{
    border-radius: 2px 0 0 2px;
}

.out-in-horizontal .ol-zoom-out{
    border-radius: 0 2px 2px 0;
}

.ol-zoom .out-in-vertical {
    display: block;
}

.out-in-vertical .ol-zoom-in{
    border-radius: 2px 2px 0 0;
}

.out-in-vertical .ol-zoom-out{
    border-radius: 0 0 2px 2px;
}