
.ol-scale-line, .ol-scale-line-night {
    background:rgba(0,60,136,0);
    border-radius:4px;
    bottom:32px;
    left:8px;
    padding:2px;
    text-align: center;
    position:absolute;
}

.ol-scale-line-inner {
    border: 1px solid #292929;
    border-bottom: 2px solid #292929;
    border-top: none;
    color: #292929;
    font-size: 10px;
    text-align: center;
    height: 4px;
    margin: 1px;
    max-width: 120px;
    will-change: contents, width;
}

.ol-scale-line-font {
    color: #292929;
    font-size: 10px;
    text-align: center;
    will-change: contents, width;
}

.ol-scale-line-night-inner {
    border: 1px solid #FFFFFF;
    border-bottom: 2px solid #FFFFFF;
    border-top: none;
    color: #FFFFFF;
    font-size: 10px;
    text-align: center;
    height: 4px;
    margin: 1px;
    will-change: contents, width;
}

.ol-scale-line-night-font {
    color: #FFFFFF;
    font-size: 10px;
    text-align: center;
    will-change: contents, width;
}

.ol-scale-line2, .ol-scale-line2-night {
    background:rgba(0,60,136,0);
    border-radius:4px;
    bottom:12px;
    left:8px;
    padding:2px;
    text-align: center;
    position:absolute;
}

.ol-scale-line2-inner {
    border: 1px solid #292929;
    border-top: 2px solid #292929;
    border-bottom: none;
    color: #292929;
    font-size: 10px;
    text-align: center;
    height: 4px;
    margin: 1px;
    max-width: 120px;
    position: relative;
    top: -16px;
    will-change: contents, width;
}

.ol-scale-line2-font {
    color: #292929;
    font-size: 10px;
    text-align: center;
    will-change: contents, width;
    position: relative;
    top: 4px;
}

.ol-scale-line2-night-inner {
    border: 1px solid #ffffff;
    border-top: 2px solid #ffffff;
    border-bottom: none;
    color: #ffffff;
    font-size: 10px;
    text-align: center;
    height: 4px;
    margin: 1px;
    position: relative;
    top: -16px;
    will-change: contents, width;
}

.ol-scale-line2-night-font {
    color: #ffffff;
    font-size: 10px;
    text-align: center;
    will-change: contents, width;
    position: relative;
    top: 4px;
}
